<script>
import CourseNav from '@/components/lms/sub/course/CourseNav.vue';
import VueDatePicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css';

export default {
  components: { VueDatePicker, CourseNav },
  data() {
    return {
      date: [null, null],
      rows: [1],
    };
  },
  methods: {
    addRow() {
      const nextRow = this.rows.length + 1;
      this.rows.push(nextRow);
    },
    delRow(index) {
      if (this.rows.length > 1) {
        this.rows.splice(index, 1);
      }
    },
  },
};
</script>

